import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    debug: true,
    state: {
        navActiveTracker: 1
    },
    mutations: {
        setNavActiveTracker (state, data) {
            state.navActiveTracker = data
        }
    }
});



